<template>
  <div>
    <h></h>
    <bbody :errvalnone="errvalnone" :getsecodbtn="getsecodbtn" :secod="secod" :gettext="gettext"    :isgoto="isgoto" :errvalue="errvalue"></bbody>
  </div>
</template>

<script type="text/ecmascript-6">
  import h from  "../components/exemptLogin/h.vue";
  import bbody from "../components/exemptLogin/bbody.vue";
  import api  from "../../apis/pagapi";
  import  "../assets/js/jquery-2.1.0"
  import "../assets/js/jq.cookie"
  export default {
    data (){
      return {
        up : {
          u: "",
          p: ""
        }
        ,
        errvalue : {
          isture : false,
          val : ""
        }
        ,
        secod : {
          secod : 60,
          secodval : "",
          istrue : false,
          isnull : true
        }
        ,
        secodbval : ""
      }
    }
    ,
    methods : {
      // 
      gettext(u,p){
        this.up.u = u;
        this.up.p = p;
      }
      ,
      //        
      _vbup(){
        var u = this.up.u.length;
        var p = this.up.p;

        //                
        if(u==""){
          this.errvalue.val = ""
          return false;
        }else if(u>11){
          this.errvalue.val = "11"
          return false;
        }
        //                 
        if(p!=this.secod.secodval){
          this.errvalue.val = ""
          return false;
        }
        if(p.length==0){
          this.errvalue.val = ""
          return false;
        }

        return true;
      }
      ,
      //
      errval : function (a,time) {
        var tant = this;
        var timd = time || 1600;
        console.log(timd);
        if(a=="1"){
          this.errvalue.isture = true;
          setTimeout(function () {
            tant.errvalue.isture = false;
            tant.errvalue.val = "";
          },timd);
        }else{
          this.errvalue.isture = false ;
        }
      }
      ,
      //
      isgoto : function () {
        //
        var a = this._vbup();
        var tant = this;
        if(a===true){
          //
          console.log("2")
          var url = `/exemptLogin?user=${this.up.u}&pass=${this.up.p}`;
          api.getpagapi(url,function (data) {
            if(data.exemptLogin == 1){
              tant.errvalue.val = " 2";
              tant.errval("1");
              setcookie('username',tant.up.u, { expires: 7 });
//                                      setTimeout(function () {
//                                        location.href = "/#/index";
//                                      },2000)
            }
            else if(data.exemptLogin == 2){
              tant.errvalue.val = "";
              tant.errval("1");
            }
            else {
              tant.errvalue.val = "";
              tant.errval("1");
            }
          })

        }else{
          //
          console.log("  ~")
          this.errval("1");
        }
        //settimeout
        return;
      }
    ,
    //6
    getsecod : function () {
          var val = "";
          for(let i=0;i<6;i++){
                var randomnum  = Number(parseInt(Math.random()*9));
                val += randomnum;
          }
          return val;
    }
    ,

    //
      getsecodbtn : function () {
        var a = this.getsecod();
        this.secod.secodval = a;

        this.errvalue.val =`  ${a}  ,60`
        this.errval("1",5000);


        this.secod.istrue = true;
        this.secod.isnull = false;
        var c = null;
        var tant = this;
        c = setInterval(function () {
              tant.secod.secod--;
              if(tant.secod.secod<=0){
                  tant.secod.secodval = "";
                  tant.secod.istrue = false;
                  tant.secod.isnull = true;
                  tant.secod.secod = 60;
                   clearInterval(c);
                  return;
              }
        },1000)
      }
      ,
//      
      errvalnone() {
            this.errvalue.isture = false;
      }
    }
    ,
    components : {
      h,
      bbody
    }

  }
</script>

<style scoped>

</style>



