<template>
  <div>
    <rh></rh>
    <rbbody  :gettext="gettext"    :isgoto="isgoto" :errvalue="errvalue"></rbbody>
  </div>
</template>

<script type="text/ecmascript-6">
  import rh from  "../components/regist/rh.vue";
  import rbbody from "../components/regist/rbbody.vue";
  import api  from "../../apis/pagapi";
  export default {
    data (){
        return {
          up : {
            u: "",
            p: ""
          }
          ,
          errvalue : {
            isture : false,
            val : ""
          }
        }
    }
    ,
    methods : {
          // 
          gettext(u,p){
            this.up.u = u;
            this.up.p = p;
          }
          ,
      //        
        _vbup(){
          var u = this.up.u.length;
          var p = this.up.p.length;

  //                
          if(u==""){
            this.errvalue.val = ""
            return false;
          }else if(u>11){
            this.errvalue.val = "11"
            return false;
          }
  //                 
          if(p==""){
            this.errvalue.val = ""
            return false;
          }else if(p>6){
            this.errvalue.val = "6"
            return false;
          }

          return true;
        }
        ,
      //
      errval : function (a) {
        var tant = this;
        if(a=="1"){
          this.errvalue.isture = true;
          setTimeout(function () {
            tant.errvalue.isture = false;
          },1600);
        }else{
          this.errvalue.isture = false ;
        }
      }
      ,
      //
      isgoto : function () {
        //
        var a = this._vbup();
        var tant = this;
        if(a===true){
          console.log("2,");
          var url = `/regist?user=${this.up.u}&pass=${this.up.p}`;
          api.getpagapi(url,function (data) {
                  console.log(data)
                  if(data.regist==1){
                    //
                    tant.errvalue.val = " 2";
                    tant.errval("1");
                    setTimeout(function () {
                          location.href = "/#/login";
                    },2000)
                  }
                  else if(data.regist==2){
                    //
                    tant.errvalue.val = "";
                    tant.errval("1");
                  }else if(data.regist==3){
                    //
                    tant.errvalue.val = "";
                    tant.errval("1");
                  }else if(data.regist==4){
                    //
                    tant.errvalue.val = "";
                    tant.errval("1");
                  }else{
                    tant.errvalue.val = "H5";
                    tant.errval("1");
                  }
          });

        }else{
          //
          console.log("  ~")
          this.errval("1");
        }
        //settimeout
        return;
      }
      ,
      //
      vgo : function (urlname,u,p,cb) {
              var url = `/${urlname}?user=${u}&pass=${p}`;
              api.getpagapi(url,function (data) {
                        cb(data);
              });
      }

    }
    ,
    components : {
      rh,
      rbbody
    }
  }
</script>

<style scoped>

</style>



