<template>
  <sou-suo :sshide="sshide" :ishistory="ishistory" :historydata="historydata" :sssave="sssave"  :ins="ins"></sou-suo>
</template>

<script type="text/ecmascript-6">
  import  SouSuo from  "../components/sousuo/SouSuo.vue";
  import  pagapi  from  "../../apis/pagapi.js";
    export default {
      data : function () {
        return {
            ishistory : true,
            historydata : []
        }
      },
      components  : {
            SouSuo
      }
      ,
      created : function () {
        var tant = this;
        this._getsousuo();
      }
      ,
      mounted : function () {
        //true
        this._findhistorydata();
      }
      ,
      methods  : {
        //
        _getsousuo : function () {
          var tant = this;
          pagapi.getpagapi("/sousuo?command=find",function (data) {
            tant.historydata=data;
            tant._findhistorydata();
          })
        }
        ,
        //
        ins :  function () {
          this.historydata=[];
          this._findhistorydata();
          var url = "/sousuo?command=remove";
          pagapi.getpagapi(url,function (data) {
            if(data.name == "remove1"){
              console.log("");
            }
            if(data.name == "remove2"){
              console.log("");
            }
          })
        }
        ,
        //
        _findhistorydata : function () {
          if(this.historydata.length>0){
            this.ishistory =false;
          }else{
            this.ishistory = true;
          }
        }
        ,
        //
        sssave : function (msg,c) {
          //
          this.historydata.push({name : msg});
          //  ishistory  
          this._findhistorydata();
          //
          c();
          //
          var url = "/sousuo?command=add&name="+msg;
          pagapi.getpagapi(url,function (data) {

            if(data.msg == "add1"){
              console.log("");
            }
            if(data.msg == "add2"){
              console.log("");
            }
          })
        }
        ,
        sshide : function () {
               history.go(-1);
        }
      }



    }


</script>

<style scoped>

</style>



