<template>
  <div>
      <h></h>
      <bbody :changeimg="changeimg" :gettext="gettext"   :lists="lists" :imgc="imgc" :isgoto="isgoto" :errvalue="errvalue"></bbody>
  </div>
</template>

<script type="text/ecmascript-6">
    import h from  "../components/login/h.vue";
    import bbody from "../components/login/bbody.vue";
    import api  from "../../apis/pagapi";

    import "../assets/js/jq.cookie"
    export default {
      data(){
        return {
          lists : [
            {num : 1,url : "src/assets/img/login/1.png",deg : "rotate(0deg)",zdeg : 0,istrue:true},
            {num : 1,url : "src/assets/img/login/1.png",deg : "rotate(90deg)",zdeg : 90,istrue:false},
            {num : 1,url : "src/assets/img/login/1.png",deg : "rotate(180deg)",zdeg : 180,istrue:false},
            {num : 1,url : "src/assets/img/login/1.png",deg : "rotate(0deg)",zdeg : 0,istrue:true}
          ]
          ,
          up : {
            u: "",
            p: ""
          }
          ,
          errvalue : {
            isture : false,
            val : ""
          }
          ,
          arrimgs : [],
          arrimg1 : []
        }
      }
      ,
      created : function () {
              this.changeimg();
      }
      ,
      mounted : function () {

      }
      ,
      methods : {
            //
            changeimg(){
                  this.arrimgs = [];
                  this.arrimg1 = [];
                    //204
                  while(true){
                          let sjnum = parseInt(Math.random()*12);
//                       
                          if(this.arrimg1.length == 4){
                               break;
                          }
//                       
                          if(this.arrimg1.length === 0){
                                this.arrimg1.push(sjnum);
                                this.arrimgs[sjnum] = 21;
                          }
                          for(let i =0; i<13; i++){
                                 if(this.arrimgs[sjnum] !== 21 ){
                                   this.arrimgs[sjnum]=21;
                                   this.arrimg1.push(sjnum);
                                 }else{
                                      break;
                                 }
                          }
                  }
                  var imgurl = this.arrimg1;
//                  
                  this.lists.forEach((item,i)=>{
                          var sj = parseInt(Math.random()*4);
//                          console.log(sj);
                          item.url =  `src/assets/img/login/${imgurl[i]}.png`;
                          item.deg = `rotate(${sj * 90}deg)`;
                          item.zdeg = `${sj * 90}`;
                  })
            }
            ,
            //
            _rotateimg : function (i) {
                var a = this.lists[i];
                this.lists[i].zdeg = Number(a.zdeg) + 90;
                this.lists[i].deg = `rotate(${this.lists[i].zdeg}deg)`;
//                console.log(this.lists[i].zdeg);
//              console.log(this.lists[i].deg);
            }
            ,
            //
            _imgistrue : function () {
                this.lists.forEach((item,i)=>{
                        var a = item.zdeg % 360;
                        if(a==0){
                              item.istrue = true;
                        }
                        else{
                              item.istrue = false;
                        }
                })
            }
            ,
            //
            _imgsrt :  function () {
                  var imgt = true;
                  var arr = this.lists;
                  for(let i =0;i<arr.length;i++){
                        var a = arr[i].istrue;
                        if(a===false){
                          return false;
                        }
                  }
                  return true;
            }
            ,
//         
            gettext(u,p){
                  this.up.u = u;
                  this.up.p = p;
            }
            ,
      //        
          _vbup(){
                  var u = this.up.u.length;
                  var p = this.up.p.length;
                  var imgs =  this._imgsrt();
                  console.log(":"+imgs)
//                
                  if(u==""){
                    this.errvalue.val = ""
                    return false;
                  }else if(u>11){
                    this.errvalue.val = "11"
                    return false;
                  }
//                 
                if(p==""){
                  this.errvalue.val = ""
                  return false;
                }else if(p>6){
                  this.errvalue.val = "6"
                  return false;
                }
                if(imgs===false){
                  this.errvalue.val = "4"
                  return false;
                }
                return true;
          }
          ,
//       
          errval : function (a) {
                var tant = this;
                if(a=="1"){
                        this.errvalue.isture = true;
                        setTimeout(function () {
                            tant.errvalue.isture = false;
                        },1600);
                }else{
                       this.errvalue.isture = false ;
                }
          }

          ,
          //
            isgoto : function () {

                  //
                  var a = this._vbup();
                  var tant = this;
                  if(a===true){
                        //
                    console.log("3")
                      var url = `/login?user=${this.up.u}&pass=${this.up.p}`;
                      api.getpagapi(url,function (data) {
                                if(data.login == 1){
                                      tant.errvalue.val = " 2";
                                      tant.errval("1");
                                      setcookie('username',tant.up.u, { expires: 7 });
//                                      setTimeout(function () {
//                                        location.href = "/#/login";
//                                      },2000)
                                }
                                else if(data.login == 2){
                                  tant.errvalue.val = "";
                                  tant.errval("1");
                                } else if(data.login == 3){
                                  tant.errvalue.val = "";
                                  tant.errval("1");
                                }
                                else {
                                  tant.errvalue.val = "";
                                  tant.errval("1");
                                }
                      })

                  }else{
                       //
                        console.log("  ~")
                        this.errval("1");
                  }
                  //settimeout
                  return;
            }
            ,
            imgc : function (i) {
                  //
                 this._rotateimg(i);
                 //
                  this._imgistrue();
            }


      }
      ,
        components : {
              h,
              bbody
        }
    }
</script>

<style scoped>

</style>



